.vetskitchen-image-text-content {
  min-height: 200px;
}
.vetskitchen-image-text-content .col-image .image {
  object-fit: cover;
  width: 100%;
}
.vetskitchen-image-text-content .acf-innerblocks-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 30px 0;
  text-align: center;
}
.vetskitchen-image-text-content .acf-innerblocks-container h1, .vetskitchen-image-text-content .acf-innerblocks-container h2, .vetskitchen-image-text-content .acf-innerblocks-container h3, .vetskitchen-image-text-content .acf-innerblocks-container h4, .vetskitchen-image-text-content .acf-innerblocks-container h5, .vetskitchen-image-text-content .acf-innerblocks-container h6, .vetskitchen-image-text-content .acf-innerblocks-container p, .vetskitchen-image-text-content .acf-innerblocks-container ul, .vetskitchen-image-text-content .acf-innerblocks-container ol {
  padding-bottom: 0;
  margin-bottom: 0;
}
.vetskitchen-image-text-content .image-right .col-content {
  order: 1;
}
.vetskitchen-image-text-content .image-right .col-image {
  order: 2;
}
@media (max-width: 767px) {
  .vetskitchen-image-text-content .col-content {
    order: 1;
  }
  .vetskitchen-image-text-content .col-image {
    order: 2;
  }
  .vetskitchen-image-text-content .acf-innerblocks-container {
    padding: 50px 0;
  }
}